
.card {

  width: 450px;
  height: 450px;
  background: grey;
  border-radius: 30px;
  background-size: cover;
  background-position: center center;
  box-shadow: 0px 10px 30px -5px rgba(19, 153, 71, 0.3);
  transition: box-shadow 0.5s;
  will-change: transform;
  
}

.card:hover {
  box-shadow: 0px 30px 100px -10px rgba(19, 153, 71, 0.3);
}

.region{
  color: #222;
}
